import React from 'react';
import './winnerPage.css';


const WinnerPage = ({time,completed,submitScore})=>{
    const displayTime = (sec)=>{
        const minutes = Math.floor(sec/60);
        const seconds = sec%60;
        return `${minutes < 10? "0"+minutes:minutes}:${seconds<10?"0"+seconds:seconds}`
    }
    return(
        <div className="winnerCard">
            <i id="flag1" className="fas fa-flag"></i><i id="trophy" className="fas fa-trophy"></i><i id="flag2" className="fas fa-flag"></i>
            <h2>Congratulations!</h2>
            <p>You are a flag expert!</p>
            <div className="progress" ><i className="fas fa-medal"></i>
            <p>{completed}/250</p>
            </div>
            
            <div className="progress" ><i className="fas fa-stopwatch"></i>
            <p>{displayTime(time)}</p>
            </div>
            <button onClick={submitScore} id="btnSubmit">Submit Score</button>
        </div>
    )
}



export default WinnerPage