import React from "react";
import LeaderBoard from "./Components/LeaderBoard";
import StartGame from "./Components/StartGame";
import "./app.css";
// ----------------Firebase Import----------------------
import firebase from 'firebase/app';
import 'firebase/database'
//------------------------------------------------------
import Main from './Components/Main';
import allCountryData from './allCountryData';

import WinnerPage from './Components/WinnerPage';
import GameOverPage from './Components/GameOverPage';

class App extends React.Component{
    constructor(){
        super()
        this.state ={
            allCountryData: [],
            remainingCountries:[],
            correctAnswer: null,
            uniqueIds: [],
           
            gameStatus: null,
            lives:3,
            completed: 0,
            time: 0,
        

            userId: null,
            username: null,

            database: [],
            leaderBoard : [],
            interval: null,
        }
    }

    componentDidMount= async()=>{
        await this.getDatabase();
        this.displayLeaderBoard();
        await this.setState({
          allCountryData: [...allCountryData],
          remainingCountries: [...allCountryData]
        })
        await this.getRandomCountry();
        this.getDummyCountries();
    
    };
    //-----------------------------Get Database-----------------------------------------
    getDatabase = ()=>{
        var firebaseConfig = {
            apiKey: 'AIzaSyC-NbuA2Om_ffqAV9wZdBekH78cgcg4MnY',
            authDomain: '',
            databaseURL: 'https://flag-game-ad0fa.firebaseio.com/',
            projectId: '',
            storageBucket: '',
            messagingSenderId: '',
            appId: ''
        };
        firebase.initializeApp(firebaseConfig);
        const database = firebase.database().ref('/users');
        this.setState({
            database:database
        })
    }
    //-------------------------------Display LeaderBoard------------------------------------
    displayLeaderBoard = ()=>{
        const database = this.state.database;
       
        database.on('value',(item)=>{
            const leaderBoard = [];
            const leaderObj = item.val();
            Object.keys(leaderObj).forEach((key)=>{
                leaderBoard.push(leaderObj[key]);
            })
            this.setState({
                leaderBoard: leaderBoard
            });
        })
    }
    //----------------------------------Set User----------------------------------------------
    setUsername=  (username)=>{
         this.setState({
            username: username,
            userId:Math.random().toString().substr(2),
            gameStatus: "playing"
        });
       
    }
    getRandomCountry =  ()=>{
        const remainingCountries =  [...this.state.remainingCountries];
        const random = Math.floor(remainingCountries.length * Math.random());
        const randomCountry = remainingCountries[random];
        remainingCountries.splice(random,1);
        this.setState({
            remainingCountries: remainingCountries,
            correctAnswer: randomCountry
        })
        
    }
    getDummyCountries = ()=>{
        const allCountryData = [...this.state.allCountryData];

        let ids = [this.state.correctAnswer.id];
        let uniqueIds = [...new Set(ids)];

        while (uniqueIds.length <3){
            ids.push(allCountryData[Math.floor(allCountryData.length * Math.random())].id);
            uniqueIds = [...new Set(ids)];
        }   
        
        const answers =[allCountryData[uniqueIds[0]],allCountryData[uniqueIds[1]],allCountryData[uniqueIds[2]]]

      
        const shuffeledAnswers = this.shuffleArray(answers)

        this.setState({
            answers: shuffeledAnswers
        })
        
    }
    //-----------------------Mixing answers after wrong answer-------------------------------------
    mixAnswers = async ()=>{
        const answers = [...this.state.answers];
        const mixedAnswers = this.shuffleArray(answers);
        await this.setState({
            answers: mixedAnswers
        });
    }
    //-----------------------Array Mixer Function From StackOverflow-------------------------------
    shuffleArray = (array)=>{
        var currentIndex = array.length, temporaryValue, randomIndex;
          
        // While there remain elements to shuffle...
        while (0 !== currentIndex) {
      
          // Pick a remaining element...
          randomIndex = Math.floor(Math.random() * currentIndex);
          currentIndex -= 1;
      
          // And swap it with the current element.
          temporaryValue = array[currentIndex];
          array[currentIndex] = array[randomIndex];
          array[randomIndex] = temporaryValue;
        }
      
        return array;
    }

    //-----------------------------------------Wrong Answer----------------------------------------------
    wrongAnswer = ()=>{
        if(this.state.gameStatus === "playing"){
            const lives = this.state.lives-1;
            if(lives < 0){
                this.gameOver();
            }else{
                this.setState({
                    lives: lives
                })
            }
        }
    }

    gameOver = ()=>{
        clearInterval(this.state.interval);
        this.setState({
            gameStatus:"lost",
            interval: null
        })
    }

    //-----------------------------------------Right Answer----------------------------------------------
    rightAnswer = async ()=>{
    
        if(this.state.remainingCountries.length > 0){
            const completed = this.state.completed + 1;
            this.setState({
                completed: completed,
            })
            await this.getRandomCountry();
            this.getDummyCountries();
        }else{
            const completed = this.state.completed + 1;
            this.setState({
                completed: completed,
            })
            this.won();
        }
     
    }
    won = ()=>{
        clearInterval(this.state.interval);
        this.setState({
            gameStatus:"won",
            interval: null
        });
    }


    // --------------------------------------Timer---------------------------------------
    timer=()=>{
        if(this.state.interval){return}
        this.setState({
            interval: setInterval(()=>{
                this.setState({
                    time: this.state.time+1
                })
            },1000)
        })
    }
    // -------------------------------------Submit Score-----------------------------------
    submitScore = async ()=>{
        const userObj = {
            "name": this.state.username,
            "score": this.state.completed,
            "time": this.state.time
        };
        const database = this.state.database;
        database.child(this.state.userId).set(userObj);

        await this.setState({
            gameStatus:null,
            time: 0,
            lives:3,
            completed:0,
            remainingCountries: [],
            correctAnswer: null,
            uniqueIds: [],
        });
        await this.setState({
            allCountryData: [...allCountryData],
            remainingCountries: [...allCountryData]
          })
        await this.getRandomCountry();
        this.getDummyCountries();
    }
    // ----------------------------------------------------------------------------------------









//-------------------------------------------------RENDER HELPER------------------------------------------------
    handleDisplay = ()=>{
        if(this.state.gameStatus ==="playing"){
            return <Main 
            mixAnswers={this.mixAnswers} 
            correctAnswer={this.state.correctAnswer} 
            answers={this.state.answers} 
            time={this.state.time}
            wrongAnswer = {this.wrongAnswer} 
            lives = {this.state.lives} 
            rightAnswer = {this.rightAnswer} 
            completed = {this.state.completed}
            />;
        }else if(this.state.gameStatus === "won"){
            return <WinnerPage completed={this.state.completed} time={this.state.time} submitScore={this.submitScore}/>;
        }else if(this.state.gameStatus === "lost"){
            return <GameOverPage completed={this.state.completed} time={this.state.time} submitScore={this.submitScore} />;
        }
        else{
            return  <div className="leaderAndStart">
                        <LeaderBoard leaderBoard={this.state.leaderBoard} />
                        <StartGame 
                        userName = {this.state.username}
                        timer={this.timer}
                        setUsername={this.setUsername}  />
                    </div> 
        }
    }
// -----------------------------------------------RENDER----------------------------------------------------------
    render(){
        return(
            <div className="container">
                {this.handleDisplay()}
            </div>
        )
    }
}

export default App;