import React from "react";
import './startGame.css';



const StartGame = ({setUsername,timer,userName})=>{
    return(
        <div className="startGame">  
            <form onSubmit={(e)=>{
                e.preventDefault();
                if(userName){
                    setUsername(userName);
                }else{
                    setUsername(e.target.username.value);
                }
                timer();
            }} 
            className="userForm">
            {userName?<p>Hello,{userName}</p>:<input placeholder="username..." name="username" id="username" required type="text" minLength="3" maxLength="5"/>}
            <button id="submitBtn" type="submit">Start Game</button>
            </form>
        </div>
    )
}


export default StartGame;