import React from 'react';
import './main.css';
import {useRef} from 'react';

//ha a componenten belol van akkor rerender eseten felulirodik
let checking = false;


const Main = ({time, answers, correctAnswer,mixAnswers,wrongAnswer,lives,rightAnswer,completed})=>{
   
    const button_0 = useRef();
    const button_1 = useRef();
    const button_2 = useRef();
    const buttons = [button_0, button_1, button_2];
    const rightSound = useRef();
    const wrongSound = useRef();
    const displayTime = (sec)=>{
        const minutes = Math.floor(sec/60);
        const seconds = sec%60;
        return `${minutes < 10? "0"+minutes:minutes}:${seconds<10?"0"+seconds:seconds}`
    }
    function checkAnswer(button){
        checking = true;
        const answerId = button.current.dataset.id;
        if(parseInt(answerId) === parseInt(correctAnswer.id)){
            button.current.classList.add("correct"); 
            rightSound.current.currentTime = 0;
            rightSound.current.play();

            const timeout = setTimeout(()=>{
                        buttons.forEach((button)=>{
                            button.current.className = "answerBtn";
                        });
                        rightAnswer();
                        checking = false;
                    },300)
        }else{
            button.current.classList.add("wrong");
            wrongSound.current.currentTime = 0;
            wrongSound.current.play();
            const timeout = setTimeout(()=>{
                    buttons.forEach((button)=>{
                        button.current.className = "answerBtn";
                    })
                    mixAnswers();
                    wrongAnswer();
                    checking = false;
                    },300)
            
        }
    }

    return(
        <div>
        <div className="topPanel">
            <span><i className="fab fa-font-awesome-flag"></i> {completed}/250</span>
            <span><i className="fas fa-heart"></i>{lives}</span>
            <span><i className="fas fa-stopwatch"></i>{displayTime(time)}</span>
        </div>
        <div className="main">
            <img id="flagImg" alt="" src={correctAnswer.flag}/>
            <div className="answers">
                <button onClick={()=>{if(!checking){checkAnswer(button_0)}} } ref={button_0} data-id={answers[0].id} className="answerBtn">{answers[0].name}</button>
                <button onClick={()=>{if(!checking){checkAnswer(button_1)} }} ref={button_1} data-id={answers[1].id} className="answerBtn">{answers[1].name}</button>
                <button onClick={()=>{if(!checking){checkAnswer(button_2)} }} ref={button_2} data-id={answers[2].id} className="answerBtn">{answers[2].name}</button>
            </div>
        </div>

        <audio ref={rightSound} src="right.wav"></audio>
        <audio ref={wrongSound} src="wrong.wav"></audio>
        </div>
    )
}

export default Main