import React from "react";
import "./leaderBoard.css";


const LeaderBoard = ({leaderBoard})=>{

    const displayTime = (sec)=>{
        const minutes = Math.floor(sec/60);
        const seconds = sec%60;
        return `${minutes < 10? "0"+minutes:minutes}:${seconds<10?"0"+seconds:seconds}`
    }
    const displayLeaderboard = ()=>{
        const leaderBoardCopy = [...leaderBoard];
        
        leaderBoardCopy.sort((a,b)=>{
            if(a.score !== b.score){
                if(a.score<b.score){
                    return 1;
                }else{
                    return -1;
                }
            }
            if(a.score === b.score){
                if(a.time>b.time){
                    return 1;
                }else{
                    return -1;
                }
            }
        })

       return leaderBoardCopy.splice(0,10).map((item,index)=>{
            if(index === 0){
                return <React.Fragment key={index}> <i id="first" className="fas fa-trophy"></i><p>{item.name}</p><p>{displayTime(item.time)}</p><p>{item.score}</p> </React.Fragment>;   
            }
            if(index === 1){
                return <React.Fragment key={index}> <i id="second" className="fas fa-medal"></i><p>{item.name}</p><p>{displayTime(item.time)}</p><p>{item.score}</p> </React.Fragment>; 
            }
            if(index === 2){
                return <React.Fragment key={index}> <i id="third" className="fas fa-medal"></i><p>{item.name}</p><p>{displayTime(item.time)}</p><p>{item.score}</p> </React.Fragment>; 
            }
            return <React.Fragment key={index}> <p>{index+1}.</p><p>{item.name}</p><p>{displayTime(item.time)}</p><p>{item.score}</p> </React.Fragment>;       
       })
    }

    
    return(
        <div className="leaderBoard">
            <i className="fa fa-trophy"></i>
            <h1>Top Players</h1>
            <div className="score">
            <h3>Nr.</h3><h3>Name</h3><h3>Time</h3><h3>Score</h3> 
                {displayLeaderboard()}
            </div>
        </div>
    )
}

export default LeaderBoard;